import { createStore } from 'vuex'

const store = createStore({
  state(){
    return {
        name : 'kim',
        age : 20,
        likes : [30,25,60],
        likeclick : false,
        more: {}
    }
  },
  mutations :{
      이름변경(state){ //실제 변경 권한은 store에만 있음 
          state.name='park';
      },
      나이변경(state,a){
        state.age+=a;
      },
      좋아요(state)
      {
          if(state.likeclick==false)
          {
              state.likes++;
              state.likeclick=true;
          }
          else{
            state.likes--;
            state.likeclick=false;
          }
      }
  },
  actions :{

  },
})

export default store